import React from 'react';
import PropTypes from 'prop-types';

PostList.propTypes = {
    posts: PropTypes.array,
};

PostList.defaultProps = {
    posts:[]
}

function PostList(props) {
    const {posts} = props
    console.log(posts);
    return (
        <div>
            <ul>
                {
                    posts.map( post => (
                        <li key={post.id}>{post.name}</li>
                    ))
                }
            </ul> 
        </div>
    );
}

export default PostList;